@import "./../Fonts"
@import "./../Colors"

.OverlappedMenu
    position: fixed
    width: 100%
    height: 100%
    left: 0px
    top: 0px

.OverlappedMenuContainar
    display: flex
    flex-direction: column
    flex-basis: 100px
    margin: 0px auto
        top: 100px
    width: 100%
    height: 100%

.OverlappedMenuTitle
    margin: 0 auto
        top: -50px
    opacity: 0
    heigtht: 50px
    color: white
    font:
        family: 'Noto Sans JP'
        size: 38px
        weight: 900

.OverlappedMenuButtons
    display: flex
    flex-direction: row
    margin: 0px auto

.OverlappedMenuCloseIconContainer
    width: 80%
    display: flex

.OverlappedMenuCloseIcon
    color: white
    font-size: 32px
    flex: 2
    cursor: pointer

.OverlappedMenuCloseIconSpace
    font-size: 32px
    width: 100px
    flex: 10

