@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Prosto+One&display=swap");
.ProfileCard {
  background-color: #aaaaaa;
  max-width: 720px;
  border-radius: 10px;
  text-align: left;
  padding-bottom: 32px;
}

.ProfileCardImage {
  position: relative;
  top: 32px;
  left: 32px;
  width: 150px;
  height: 150px;
  border-radius: 75px;
}

.ProfileCardTitle {
  text-align: left;
  margin-top: -100px;
  margin-left: 80px;
  color: #263159;
}
.ProfileCardTitle h1 {
  position: relative;
  margin: 0px;
  left: 182px;
  font-family: "Fredoka One", cursive;
  font-size: calc(64px + 1vmin);
  letter-spacing: 15px;
}
.ProfileCardTitle h2 {
  position: relative;
  margin: 0px;
  top: -24px;
  left: 182px;
  font-family: "Noto Sans JP";
  font-size: calc(20px + 1vmin);
  font-weight: 500;
}

.ProfileCardDescription {
  justify-content: center;
  text-align: left;
  color: #263159;
  margin-top: 40px;
  margin-bottom: 20px;
}
.ProfileCardDescription p {
  margin: 0px;
  margin-left: 32px;
  margin-right: 32px;
  word-break: normal;
  font-family: "Noto Sans JP";
  font-size: calc(10px + 1vmin);
  font-weight: 700;
}

.ProfileCardLink {
  height: 48px;
  display: flex;
  align-items: center;
  color: #FFFBEB;
  background-color: #263159;
  margin: 4px auto;
  margin-left: 10px;
  margin-right: 10px;
  padding-right: 10px;
  cursor: pointer;
  border-radius: 30px;
  border-opacity: 1;
  border-color: black;
  border-style: solid;
  border-width: 2px;
}
.ProfileCardLink p {
  margin: 0 auto;
  margin-left: 8px;
  margin-right: 4px;
  font-family: "Noto Sans JP";
  font-size: 20px;
  font-weight: 700;
}

.MiniProfileCard {
  background-color: #aaaaaa;
  width: 80vw;
  min-height: 400px;
  border-radius: 10px;
  text-align: center;
}

.MiniProfileCardImage {
  margin: 20px;
  margin-bottom: 0px;
  width: 150px;
  height: 150px;
  border-radius: 75px;
}

.MiniProfileCardTitle {
  text-align: center;
  color: #263159;
}
.MiniProfileCardTitle h1 {
  margin: 0px;
  font-family: "Fredoka One", cursive;
  font-size: 32px;
  letter-spacing: 6px;
}
.MiniProfileCardTitle h2 {
  margin: 0px;
  margin-top: -12px;
  font-family: "Noto Sans JP";
  font-size: 24px;
  font-weight: 500;
}

.MiniProfileCardDescription {
  text-align: left;
  color: #263159;
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
}
.MiniProfileCardDescription p {
  margin: 0px;
  margin-left: 32px;
  margin-right: 32px;
  word-break: normal;
  font-family: "Noto Sans JP";
  font-size: calc(14px + 1vmin);
  font-weight: 700;
}

.MiniProfileCardLink {
  display: flex;
  align-items: center;
  width: 48px;
  color: #FFFBEB;
  margin: 10px;
  background-color: #263159;
  cursor: pointer;
  border-opacity: 1;
  border-color: black;
  border-style: solid;
  border-width: 2px;
  border-radius: 30px;
}

.ProfileLinkIconList {
  width: 80%;
  margin-left: 10%;
  height: auto;
  justify-content: center;
}

.ProfileLinkIconListFloatLeft {
  float: left;
}/*# sourceMappingURL=ProfileCard.css.map */