@import './../Fonts'
@import './../Colors'

.BaseBoard
    &.All
        margin:
            top: 0px
        background:
            color: $bgColor
    &.Main
        background-color: $baseColor
        margin:
            top: 5vh
        min-height: 90vh
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        font:
            family: 'Noto Sans JP'
            size: calc(20px + 2vmin)
            weight: 900
    &.Menu
        position: fixed
        background-color: $bgColor
        display: flex
        place-items: center
        top: 0px
        height: 5vh
        width: 100vw
        justify-content: space-between
    &.SubFooter
        background-color: $subColor
        min-height: 0.5vh
    &.Footer
        color: white
        min-height: 4.5vh
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        font:
            family: 'Noto Sans JP'
            size: calc(4px + 1vmin)
            weight: 100
