@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Prosto+One&display=swap");
.VButton {
  position: relative;
  margin: 4px;
  padding: 4px;
  padding-left: 6px;
  padding-right: 6px;
  background-color: #495579;
  min-width: 80px;
  height: 32px;
  cursor: pointer;
  opacity: 0;
}

.title {
  margin-top: -32px;
  margin-left: 6px;
  padding-top: 2px;
  font-family: "Noto Sans JP";
  font-size: 16px;
  font-weight: 700;
}

.line {
  height: 100%;
  background-color: #FFFBEB;
}

.LineButton {
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
  padding: 4px;
  padding-left: 6px;
  padding-right: 6px;
  width: 100px;
  height: 3vh;
  cursor: pointer;
  opacity: 0;
}

.LineLine {
  position: absolute;
  top: 32px;
  left: 0px;
  height: 6%;
  background-color: #FFFBEB;
}

.LineTitle {
  text-align: center;
  font-family: "Noto Sans JP";
  font-size: 20px;
  font-weight: 700;
}/*# sourceMappingURL=Button.css.map */