.ProductDescription {
  width: 80vw;
  text-align: left;
  margin-top: 8vh;
  color: #263159;
  font-family: "Noto Sans JP";
  font-size: calc(5px + 5vmin);
  font-weight: 800;
}

.ProductPage {
  margin: 0 auto;
  padding-top: 1vmin;
  padding-bottom: 2vmin;
  width: 80vw;
  background-color: #fffdf5;
}

.WorkCard {
  float: left;
  max-width: 280px;
  margin: 10px;
  font-family: "Noto Sans JP";
  font-size: 20px;
  font-weight: 500;
}

.WorkCardMain {
  min-height: 450px;
  background-color: white;
  border-radius: 8px;
  border-style: solid;
  border-width: 2px;
  padding: 10px;
  text-align: center;
}

.WorkCardContentIllust {
  min-width: 200px;
  min-height: 200px;
}
.WorkCardContentIllust img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.WorkCardTypeLabel {
  padding-bottom: 6px;
  width: 100%;
}

.WorkCardTypeDescription {
  font-weight: 900;
  font-size: 20px;
}

.WorkCardTypeLabelIcon {
  width: 80px;
  color: black;
  font-size: 12px;
  font-weight: 900;
  border-style: solid;
  border-width: 2px;
  border-color: black;
  border-radius: 20px;
}/*# sourceMappingURL=WorkCard.css.map */