@import './../Colors'
@import './../Fonts'

.ProfileCard
    background:
        color: $grayColor
    max-width: 660px
    border-radius: 10px
    text-align: left
    padding:
        bottom: 32px

.ProfileCardImage
    position: relative
    top: 32px
    left: 32px
    width: 150px
    height: 150px
    border:
        radius: 75px

.ProfileCardTitle
    text-align: left
    margin:
        top: -100px 
        left: 40px
    color: $bgColor
    h1
        position: relative
        margin: 0px
        left: 150px + 32px
        font:
            family: 'Fredoka One', cursive
            size: calc(64px + 1vmin)
        letter:
            spacing: 15px
    h2
        position: relative
        margin: 0px
        top: -24px
        left: 150px + 32px
        font:
            family: 'Noto Sans JP'
            size: calc(20px + 1vmin)
            weight: 500

.ProfileCardDescription
    justify-content: center
    text-align: left
    color: $bgColor
    margin:
        top: 40px        
        bottom: 20px
    p
        margin: 0px
            left: 32px
            right: 32px
        word-break: normal
        font:
            family: 'Noto Sans JP'
            size: calc(10px + 1vmin)
            weight: 700

.ProfileCardLink
    height: 48px
    display: flex
    align-items: center
    color: $baseColor
    background:
        color: $bgColor
    margin: 4px auto
        left: 10px
        right: 10px
    padding:
        right: 10px
    cursor: pointer
    border:
        radius: 30px
    border:
        opacity: 1
        color: black
        style: solid
        width: 2px
    p
        margin: 0 auto
            left: 8px
            right: 4px
        font:
            family: 'Noto Sans JP'
            size: 20px
            weight: 700


.MiniProfileCard
    background:
        color: $grayColor
    width: 80vw
    min-height: 400px
    border-radius: 10px
    text-align: center

.MiniProfileCardImage
    margin: 20px
        bottom: 0px
    width: 150px
    height: 150px
    border:
        radius: 75px

.MiniProfileCardTitle
    text-align: center
    color: $bgColor
    h1
        margin: 0px
        font:
            family: 'Fredoka One', cursive
            size: 32px
        letter:
            spacing: 6px
    h2
        margin: 0px
            top: -12px
        font:
            family: 'Noto Sans JP'
            size: 24px
            weight: 500

.MiniProfileCardDescription
    text-align: left
    color: $bgColor
    width: 100% 
    justify-content: center
    align-items: center
    margin: 0 auto
        top: 20px
        bottom: 20px 
    p
        margin: 0px
            left: 32px
            right: 32px
        word-break: normal
        font:
            family: 'Noto Sans JP'
            size: calc(14px + 1vmin)
            weight: 700

.MiniProfileCardLink
    display: flex
    align-items: center
    width: 48px
    color: $baseColor
    margin: 10px
    background:
        color: $bgColor
    cursor: pointer
    border:
        opacity: 1
        color: black
        style: solid
        width: 2px
        radius: 30px

.ProfileLinkIconList
    width: 80%
    margin:
        left: 10%
    height: auto
    justify-content: center

.ProfileLinkIconListFloatLeft
    float: left