@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Prosto+One&display=swap");
.OverlappedMenu {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 0px;
}

.OverlappedMenuContainar {
  display: flex;
  flex-direction: column;
  flex-basis: 100px;
  margin: 0px auto;
  margin-top: 100px;
  width: 100%;
  height: 100%;
}

.OverlappedMenuTitle {
  margin: 0 auto;
  margin-top: -50px;
  opacity: 0;
  heigtht: 50px;
  color: white;
  font-family: "Noto Sans JP";
  font-size: 38px;
  font-weight: 900;
}

.OverlappedMenuButtons {
  display: flex;
  flex-direction: row;
  margin: 0px auto;
}

.OverlappedMenuCloseIconContainer {
  width: 80%;
  display: flex;
}

.OverlappedMenuCloseIcon {
  color: white;
  font-size: 32px;
  flex: 2;
  cursor: pointer;
}

.OverlappedMenuCloseIconSpace {
  font-size: 32px;
  width: 100px;
  flex: 10;
}/*# sourceMappingURL=OverlappedMenu.css.map */