@import "../Colors"

.ProductDescription
    width: 80vw
    text-align: left
    margin:
        top: 8vh
    color: $bgColor
    font:
        family: 'Noto Sans JP'
        size: calc(5px + 5vmin)
        weight: 800

.ProductPage
    margin: 0 auto
    padding:
        top: 1vmin
        bottom: 2vmin
    width: 80vw
    background:
        color: lighten($baseColor, 2%)


.WorkCard
    float: left
    max-width: 280px
    margin: 10px
    font:
        family: 'Noto Sans JP'
        size: 20px
        weight: 500
    

.WorkCardMain
    min-height: 450px
    background:
        color: white
    border:
        radius: 8px
        style: solid
        width: 2px
    padding: 10px
    text-align: center

.WorkCardContentIllust
    min-width: 200px
    min-height: 200px
    img
        width: 100%
        height: auto
        border:
            radius: 8px

.WorkCardTypeLabel
    padding:
        bottom: 6px
    width: 100%

.WorkCardTypeDescription
    font:
        weight: 900
        size: 20px

.WorkCardTypeLabelIcon
    width: 80px
    color: black
    font:
        size: 12px
        weight: 900
    border:
        style: solid
        width: 2px
        color: black
        radius: 20px
