@import './../Colors'
@import './../Fonts'

.VButton
    position: relative
    margin: 4px
    padding: 4px
        left: 6px
        right: 6px
    background:
        color: $subColor
    min-width: 80px
    height: 32px
    cursor: pointer
    opacity: 0

.title
    margin:
        top: -32px
        left: 6px
    padding:
        top: 2px
    font:
        family: 'Noto Sans JP'
        size: 16px
        weight: 700
.line
    height: 100%
    background:
        color: $baseColor

.LineButton
    position: relative
    margin:
        left: 10px
        right: 10px
    padding: 4px
        left: 6px
        right: 6px
    width: 100px
    height: 3vh
    cursor: pointer
    opacity: 0

.LineLine
    position: absolute
    top: 32px
    left: 0px
    height: 6%
    background:
        color: $baseColor

.LineTitle
    text-align: center
    font:
        family: 'Noto Sans JP'
        size: 20px
        weight: 700
